import React from "react";
// import { ListGroup } from "react-bootstrap";
import { Link } from "react-router-dom";
import IssuesLabels from "./IssuesLabels";
import { format } from 'timeago.js';


function issuesItem(props) {
  const commentCount = props.comments;
  const commentContent = <p style={{margin: "0"}}>
  {<svg
  version="1.1"
  id="Capa_1"
  x="0px"
  y="0px"
  viewBox="0 0 29.338 29.338"
  style={{enableBackground:"new 0 0 29.338 29.338", width: "13px"}}
>
  <g>
    <path
      style={{fill:"#010002"}}
      d="M27.184,1.605H2.156C0.967,1.605,0,2.572,0,3.76v17.572c0,1.188,0.967,2.155,2.156,2.155h13.543
l5.057,3.777c0.414,0.31,0.842,0.468,1.268,0.468c0.789,0,1.639-0.602,1.637-1.923v-2.322h3.523c1.188,0,2.154-0.967,2.154-2.155
V3.76C29.338,2.572,28.371,1.605,27.184,1.605z M27.34,21.332c0,0.085-0.068,0.155-0.154,0.155h-5.523v3.955l-5.297-3.956H2.156
c-0.086,0-0.154-0.07-0.154-0.155V3.759c0-0.085,0.068-0.155,0.154-0.155v0.001h25.029c0.086,0,0.154,0.07,0.154,0.155
L27.34,21.332L27.34,21.332z M5.505,10.792h4.334v4.333H5.505C5.505,15.125,5.505,10.792,5.505,10.792z M12.505,10.792h4.334v4.333
h-4.334V10.792z M19.505,10.792h4.334v4.333h-4.334V10.792z"
    />
  </g>
</svg>}{" "}{commentCount}</p>;
  // const lables = <span><IssuesLabels labels={props.labels} /></span>;
  const issueTitle = <span>{props.title} <IssuesLabels labels={props.labels} /></span>;

  return (
    <div key={props.id}>
      <Link key={props.id}
        to={{
          pathname: `comments/${props.number}`,
          state: {
            title: props.title,
            number: props.number,
            userDetails: props.user,
            time: props.time,
            commentCount: props.comments,
            state: props.state,
            description: props.description,
          },
        }}
        style={{ textDecoration: "none", color: "black" }}
      >
        <div className="issueItem" key={props.id}>
          <div className="green-icon">
            <svg
              aria-hidden="true"
              className="octicon mr-1 text-success"
              height="16"
              role="img"
              viewBox="0 0 14 16"
              width="14"
              style={{
                color: "Green",
                fill: "currentcolor",
                userSelect: "none",
              }}
            >
              <path
                fillRule="evenodd"
                d="M7 2.3c3.14 0 5.7 2.56 5.7 5.7s-2.56 5.7-5.7 5.7A5.71 5.71 0   0 1 1.3 8c0-3.14 2.56-5.7 5.7-5.7zM7 1C3.14 1 0 4.14 0 8s3.14 7 7 7 7-3.14 7-7-3.14-7-7-7zm1 3H6v5h2V4zm0 6H6v2h2v-2z"
              ></path>
            </svg>
          </div>
          <div style={{width: "93%", padding: "0 1.5%"}}>
            <div className="issue-title">
              {issueTitle}
            </div>
            <div className="issue-desc">
              #{props.number} {props.state}ed {format(`${props.time}`)} by {" "}
              {props.user.login}
            </div>
          </div>
          <div style={{width: "2%"}}>
            {commentCount ? commentContent : ' '}
          </div>
        </div>
      </Link>
    </div>
  );
}

export default issuesItem;
