import React, { useEffect, useState } from "react";
import { Link, useLocation } from "react-router-dom";
import { useParams } from "react-router";
import { format } from "timeago.js";
import CommentItem from "../components/CommentItem";

function Comments(props) {
  const param = useParams();
  const location = useLocation();

  const [isloading, setIsLoading] = useState(false);
  const [commentData, setcommentData] = useState([]);

  useEffect(() => {
    setIsLoading(true);
    fetch(
      `https://api.github.com/repos/facebook/react/issues/${param.issueId}/comments`
    )
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setIsLoading(false);
        // console.log(data);
        setcommentData(data);
      });
  }, [param.issueId]);

  if (isloading) {
    return (
      <section>
        <h2 style={{ textAlign: "center" }}>Loading...</h2>
      </section>
    );
  }

  return (
    <section className="container">
      <Link className="btn btn-success" to={""} style={{marginLeft: "5px"}}>
        Back
      </Link>
      <div style={{padding: "0 5px"}}>
        <p className="comment-title">
          {location.state.title}
          <span style={{ color: "#6c757d" }}> #{location.state.number}</span>
        </p>
      </div>
      <div style={{display:"flex", padding: "0 5px"}}>
        <div className="btn btn-success" style={{ paddingBottom: "10px" }}>
          <span>
            <svg
              aria-hidden="true"
              className="octicon mr-1 text-success"
              height="16"
              role="img"
              viewBox="0 0 14 16"
              width="14"
              style={{
                color: "white",
                display: "inline-block",
                fill: "currentcolor",
                userSelect: "none",
                verticalAlign: "middle",
                alignItems: "start",
              }}
            >
              <path
                fillRule="evenodd"
                d="M7 2.3c3.14 0 5.7 2.56 5.7 5.7s-2.56 5.7-5.7 5.7A5.71 5.71 0   0 1 1.3 8c0-3.14 2.56-5.7 5.7-5.7zM7 1C3.14 1 0 4.14 0 8s3.14 7 7 7 7-3.14 7-7-3.14-7-7-7zm1 3H6v5h2V4zm0 6H6v2h2v-2z"
              ></path>
            </svg>
          </span>{" "}
          {location.state.state}
        </div>
        <div style={{ color: "#6c757d", marginLeft: "1rem", paddingTop: "0.4rem"}}>
          {" "}
          <b>{location.state.userDetails.login}</b> opened this issue{" "}
          {format(`${location.state.time}`)}.{" "}
          <span style={{ textDecoration: "underline" }}>
            {location.state.commentCount} comments
          </span>
        </div>
      </div>

      <div className="issue-body">
        <div
          className="outer-user-avtar"
          style={{
            minWidth: "87px",
            maxWidth: "87px",
            height: "87px",
            marginRight: "1rem",
          }}
        >
          <img
            className="img-fluid"
            style={{ borderRadius: "10px" }}
            src={location.state.userDetails.avatar_url}
            alt="user-diplay-avtar"
          />
        </div>
        <div
          style={{
            border: "1px solid #bbb",
            width: "100%",
            borderRadius: "10px",
          }}
        >
          <div className="card-title">
            <div
              className="inner-user-avtar"
              style={{
                minWidth: "70px",
                maxWidth: "70px",
                height: "70px",
                margin: ".6rem",
              }}
            >
              <img
                className="img-fluid"
                style={{ borderRadius: "10px" }}
                src={location.state.userDetails.avatar_url}
                alt="user-diplay-avtar"
              />
            </div>
            <p>
              {" "}
              <b>{location.state.userDetails.login}</b> Commented{" "}
              {format(`${location.state.time}`)}.
            </p>
          </div>
          <div className="card-body">
            <p>{location.state.description}</p>
          </div>
        </div>
      </div>

      <CommentItem comments={commentData} />
    </section>
  );
}

export default Comments;
