import React from "react";
import { format } from "timeago.js";

const CommentItem = (props) => {
  return (
    <div>
      {props.comments.map((comment) => (
        <div className="issue-body" key={comment.id}>
          <div
            className="outer-user-avtar"
            style={{
              minWidth: "87px",
              maxWidth: "87px",
              height: "87px",
              marginRight: "1rem",
            }}
          >
            <img
              className="img-fluid"
              style={{ borderRadius: "10px" }}
              src={comment.user.avatar_url}
              alt="user-diplay-avtar"
            />
          </div>
          <div
            style={{
              border: "1px solid #bbb",
              width: "100%",
              borderRadius: "10px",
            }}
          >
            <div className="card-title">
              <div
                className="inner-user-avtar"
                style={{
                  minWidth: "70px",
                  maxWidth: "70px",
                  height: "70px",
                  margin: ".6rem",
                }}
              >
                <img
                  className="img-fluid"
                  style={{ borderRadius: "10px" }}
                  src={comment.user.avatar_url}
                  alt="user-diplay-avtar"
                />
              </div>
              <p>
                {" "}
                <b>{comment.user.login}</b> Commented{" "}
                {format(`${comment.created_at}`)}.
              </p>
            </div>
            <div className="card-body">
              <p>{comment.body}</p>
            </div>
          </div>
        </div>
      ))}
    </div>
  );
};

export default CommentItem;
